.footer .footer_nav ul { display: flex; width: 100%; justify-content: space-between; align-items: center; }

.footer .footer_nav ul li:first-child { margin-left: 0; }

.footer .footer_nav ul a { font-family: "OpenSans", sans-serif; font-weight: 400; font-size: 15px; color: #FFFFFF; transition: color .3s; }

.footer .footer_nav ul a:hover { transition: color .3s; color: #82B056; }

@media only screen and (max-width: 992px) { .footer .footer_nav { display: none; } }

#form { position: relative; margin: 10% auto 0; max-width: 570px; padding: 60px 100px 20px; background: url("../img/modal-bg.jpg") center no-repeat; background-size: cover; border-radius: 10px; }

@media only screen and (max-width: 576px) { #form { padding: 30px 10px 10px; margin: 2% auto 0; } }

#form h3 { font-family: "SegoePrint", serif; font-weight: 400; font-size: 36px; color: #252B2F; text-align: center; margin-bottom: 45px; }

#form p { font-family: "OpenSans", sans-serif; font-weight: 400; font-size: 10px; color: #63676B; text-align: center; }

#form p a { text-decoration: underline; color: #63676B; }

#form p a:hover { color: #252B2F; }

#form input[type="text"], #form input[type="tel"], #form input[type="email"], #form textarea, #form button { display: block; margin: 0 auto; text-align: center; font-size: 16px; color: #63676B; line-height: 3; }

#form input[type="text"]::placeholder, #form input[type="tel"]::placeholder, #form input[type="email"]::placeholder, #form textarea::placeholder, #form button::placeholder { color: #63676B; font-size: 16px; font-family: "OpenSans", sans-serif; font-weight: 400; }

#form input[type="text"], #form input[type="tel"], #form input[type="email"] { margin-bottom: 10px; width: 100%; max-width: 370px; }

#form input[type="email"] { margin-bottom: 20px; }

#form textarea { width: 100%; max-width: 370px; border-color: #E8E6DE; resize: none; line-height: 2.4; font-family: "OpenSans", sans-serif; font-size: 16px; font-weight: 400; margin-bottom: 30px; }

#form label { max-width: 370px; margin: 0 auto; display: block; }

#form label.radio { text-align: center; font-family: "OpenSans", sans-serif; font-weight: 400; font-size: 16px; color: #63676B; margin-bottom: 30px; }

#form label.radio:last-child { margin-bottom: 50px; }

#form label.error { position: absolute; left: 50%; transform: translateX(-50%) translateY(-20px); background: #CC1F32; padding: 2px 10px; color: #FFFFFF; border-radius: 15px; width: 200px; }

#form label.error::before { content: ''; position: absolute; width: 10px; height: 10px; background: #CC1F32; top: -5px; left: 20px; transform: translateX(-50%) rotate(45deg); }

#form button { background: #CC1F32; color: #FFFFFF; line-height: 3; font-family: "OpenSans", sans-serif; font-weight: 400; font-size: 16px; padding: 0 45px; border-radius: 25px; margin-bottom: 35px; }

#form_background { transition: all 0.3s 0.3s; }

#form, #form_wrapper { transition: all 0.3s ease-out; }

#form { transform: translateX(0) translateY(-40%); }

.popup_visible #form { transform: translateX(0) translateY(0); }

.form_close { position: absolute; top: 5px; right: 15px; font-size: 24px; cursor: pointer; }

#thanks { padding: 60px 90px; background: url("../img/modal-bg.jpg") center no-repeat; background-size: cover; margin: 10% auto 0; }

#thanks p { margin-bottom: 0; }
